.s-services-cards {
    overflow: hidden;
    padding: rem(40) 0;

    @media (min-width: 768px) {
        padding: rem(50) 0;
    }

    @media (min-width: 1024px) {
        padding: rem(60) 0;
    }

    @media (min-width: 1600px) {
        padding: rem(75) 0;
    }

    &__items {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -15px;
        margin-bottom: -70px;

        @media (max-width: 1199px) {
            margin-bottom: -105px;
        }

        @media (max-width: 600px) {
            margin-bottom: -55px;
        }
    }

    &__title {
        color: #9A9EA9;
        margin-bottom: 197px;

        @media (max-width: 1699px) {
            margin-bottom: 178px;
        }

        @media (max-width: 1199px) {
            margin-bottom: 227px;
        }

        @media (max-width: 991px) {
            margin-bottom: 110px;
        }

        @media (max-width: 600px) {
            margin-bottom: 66px;
        }
    }

    .service-card {
        order: 1;
        width: calc(25% - 30px);
        margin: 0 15px;
        margin-bottom: 70px;

        @media (max-width: 1199px) {
            width: calc(33.3333% - 30px);
            margin-bottom: 105px;
        }

        @media (max-width: 600px) {
            width: calc(50% - 20px);
            margin: 0 10px;
            margin-bottom: 55px;
        }
    }

    &__btn {
        order: 8;
        display: flex;
        align-items: center;
        justify-content: center;
        width: calc(50% - 30px);
        margin: 0 15px;

        @media (max-width: 1199px) {
            min-height: 100%;
            margin-bottom: 105px;
        }

        @media (max-width: 600px) {
            order: 10;
            width: calc(50% - 20px);
            margin: 0 10px;
            margin-bottom: 55px;
        }
    }

    &__texts {
        order: 9;
        display: flex;
        flex-direction: column;
        width: calc(50% - 30px);
        margin: 0 15px;
        margin-bottom: 50px;

        @media (max-width: 1199px) {
            margin-left: auto;
            margin-bottom: 105px;
        }

        @media (max-width: 991px) {
            width: calc(66.6667% - 30px);
        }

        @media (max-width: 600px) {
            width: 100%;
            margin: 0 10px;
            margin-bottom: 55px;
        }

        &-title {
            font-size: 40px;
            line-height: 120%;
            text-align: center;
            margin-bottom: 20px;

            @media (max-width: 1699px) {
                font-size: 38px;
            }

            @media (max-width: 1199px) {
                text-align-last: left;
                font-size: 35px;
                margin-bottom: 16px;
            }

            @media (max-width: 991px) {
                font-size: 25px;
                margin-bottom: 14px;
            }
        }

        &-p {
            max-width: 600px;
            margin: 0 auto;

            @media (max-width: 1199px) {
                max-width: 463px;
            }

            @media (max-width: 991px) {
                max-width: 100%;
            }

            p {
                font-size: 21px;
                line-height: 120%;
                margin-bottom: 20px;

                @media (max-width: 1699px) {
                    font-size: 19px;
                }

                @media (max-width: 1199px) {
                    font-size: 15px;
                    margin-bottom: 16px;
                }

                @media (max-width: 991px) {
                    font-size: 12px;
                }

                @media (max-width: 600px) {
                    font-size: 14px;
                }

                &:last-child {
                    margin-bottom: 0;
                }
            }
        }

        &-btn {
            display: flex;
            justify-content: center;
            margin-top: auto;

            @media (max-width: 1699px) {
                margin-top: 42px;
            }

            @media (max-width: 1199px) {
                margin-top: 31px;
            }

            @media (max-width: 991px) {
                margin-top: 23px;
            }

            @media (max-width: 600px) {
                margin-top: 40px;
            }
        }
    }

    .btn {
        padding: 10px 30px;

        @media (max-width: 1199px) {
            font-size: 12px;
            padding: 15px 40px;
        }

        @media (max-width: 600px) {
            width: 100%;
            max-width: 100%;
            padding: 15px 0;
        }
    }
}
