@import "helpers/index";
@import "varibles/index";
@import "../blocks/modules/services/s_services_cards/s_services_cards";
@import "../blocks/modules/support/s_top_menu/s_top_menu.scss";

.s-banner {
    overflow: hidden;

    &__title {
        max-width: rem(1050);
        margin-bottom: rem(89);
        color: #9a9ea9;
        -webkit-text-fill-color: #9a9ea9;

        @media (min-width: 768px) {
            margin-bottom: rem(40);
        }

        @media (min-width: 1024px) {
            margin-bottom: rem(50);
        }

        @media (min-width: 1600px) {
            margin-bottom: rem(100);
        }

        @media (min-width: 1920px) {
            margin-bottom: rem(90);
        }
    }
}

.service-page-1 {
    .service-card {
        &:last-child {
            order: 9;
            margin-top: 50px;
            margin-right: auto;

            @media (max-width: 1199px) {
                margin-left: auto;
                margin-right: 15px;
                margin-top: 0;
            }
        }
    }
}

.service-page-2 {
    .service-card {
        &:last-child {
            .service-card {
                &__number {
                    @media (max-width: 1199px) {
                        top: -55%;
                    }

                    @media (max-width: 600px) {
                        top: -65%;
                    }
                }
            }
        }
    }
}
